import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

export const getAcademyData = createAsyncThunk(
  "getAcademyData",
  async (req, body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      const { data } = await axios.get(
        `${API_URL}/api/academy/details/${req.token}`,
        body,
        config
      );
      if (!data.error) {
        return await data;
      } else {
        return "";
      }
    } catch (error) {
      console.log(error);
    }
  }
);

export const updateAcademyReducer = createAsyncThunk(
  "updateAcademy",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
      },
    };
    const { data } = await axios.post(
      `${API_URL}/api/academy/update/`,
      body,
      config
    );
    return await data;
  }
);

export const createAcademyReducer = createAsyncThunk(
  "createAcademy",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
      },
    };
    const { data } = await axios.post(
      `${API_URL}/api/academy/create/`,
      body,
      config
    );
    return await data;
  }
);

export const deleteAcademyReducer = createAsyncThunk(
  "deleteAcademy",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
      },
    };
    const { data } = await axios.post(
      `${API_URL}/api/academy/delete/`,
      body,
      config
    );
    return await data;
  }
);

const initialState = {};

const AcademyReducer = createSlice({
  name: "AcademyReducer",
  initialState,
  reducers: {},
  extraReducers: {
    [getAcademyData.fulfilled]: (state, action) => {
      state.academyLoading = false;
      if (action.payload.error) {
        state.academyError = action.payload.error;
        state.academySuccess = "";
      } else {
        state.academy = action.payload;
        state.success = "";
      }
    },
    [getAcademyData.pending]: (state, action) => {
      state.academyLoading = true;
      state.academy = null;
      state.academySuccess = "";
      state.academyError = "";
    },
    [getAcademyData.rejected]: (state, action) => {
      state.academyLoading = false;
      if (action.payload.error) {
        state.academy = null;
        state.academyError = action.payload.error;
      }
    },
    [updateAcademyReducer.fulfilled]: (state, action) => {
      state.academyLoading = false;
      if (action.payload.error) {
        state.academyError = action.payload.error;
        state.academySuccess = "";
      } else {
        state.academy = action.payload;
        state.academySuccess = "Academy data updated successful!!";
      }
    },
    [updateAcademyReducer.pending]: (state, action) => {
      state.academyLoading = true;
      state.academy = null;
      state.academySuccess = "";
      state.academyError = "";
    },
    [updateAcademyReducer.rejected]: (state, action) => {
      state.academyLoading = false;
      if (action.payload.error) {
        state.academy = null;
        state.academyError = action.payload.error;
      }
    },
  },
});

export default AcademyReducer.reducer;
